import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import BlcLabel from 'components/BlcText/Label';
import BlcInputError from 'components/BlcInput/Error';
import { BlcIconCheck, BlcIconRadio } from 'components/BlcIcon';
import { color, size, unit } from 'public/styles';

const BlcCheckboxBase = (props) => {
	return (
		<div className="form-item">
			<BlcLabel
				cursor="pointer"
				display="flex"
				for={props.id}
				padding={size.marginSmallest}
			>
				<input
					id={props.id}
					key={props.id}
					name={props.name}
					type={props.type}
					className={
						props.errors[props.name] && props.touched[props.name]
							? 'field-error'
							: null
					}

					onBlur={props.handleBlur}
					onChange={event => props.setFieldValue(props.name, event.target.type === 'checkbox' ? event.target.checked : event.target.value)}

					defaultChecked={props.checked}
					value={props.type === 'radio' ? props.value : props.values[props.name]}
					disabled={props.disabled}
				/>

				<span>
					{props.checked === true
						? <Fragment>
							<small>&#x2611;</small>
							{props.type === 'checkbox'
								?
								<BlcIconCheck
									color={
										props.errors[props.name] && props.touched[props.name]
											? color.alert
											: null
									}
									size="inherit"
								/>
								:
								<BlcIconRadio
									color={
										props.errors[props.name] && props.touched[props.name]
											? color.alert
											: null
									}
									size="inherit"
								/>
							}
						</Fragment>
						: <Fragment>
							<small>&#x2610;</small>
						</Fragment>
					}
				</span>

				{props.text}

			</BlcLabel>

			{props.errors[props.name] && props.touched[props.name]
				? <BlcInputError>{props.errors[props.name]}</BlcInputError>
				: null
			}

			<style jsx>{`
				.form-item {
					margin: ${props.margin} 0;
				}

				.form-item:focus input ~ span,
				.form-item:hover input ~ span {
					border-color: ${color.asphaltLight};
				}

				.form-item input:disabled ~ span {
					color: ${color.gray};
					cursor: not-allowed;
				}

				.form-item:focus input:disabled ~ span,
				.form-item:hover input:disabled ~ span {
					border-color: ${color.grayLight};
				}

				input {
					appearance: none;
					height: 0;
					opacity: 0;
					padding: 0;
					position: absolute;
					width: 0;
				}

				input.field-error ~ span {
					border-color: ${color.alert};
				}

				span {
					align-self: center;
					background-color: ${color.white};
					border-color: ${color.asphalt};
					border-radius: ${props.type === 'checkbox' ? '0' : '50%'};
					border-style: solid;
					border-width: ${size.border};
					display: block;
					height: ${unit(2)};
					margin-right: ${size.marginSmallest};
					width: ${unit(2)};
				}

				small { display: none; }
			`}</style>
		</div>
	);
}

BlcCheckboxBase.propTypes = {
	id: PropTypes.string.isRequired,
	name: PropTypes.string.isRequired,
	text: PropTypes.string.isRequired,

	errors: PropTypes.object,
	onBlur: PropTypes.func,
	onChange: PropTypes.func,
	touched: PropTypes.object,
	values: PropTypes.object,

	color: PropTypes.string,
	disabled: PropTypes.bool,
	margin: PropTypes.string,
	type: PropTypes.oneOf(['checkbox', 'radio'])
};

BlcCheckboxBase.defaultProps = {
	disabled: false,
	margin: size.marginSmallest,
	type: 'checkbox'
};

export default BlcCheckboxBase;


