import React from 'react';
import PropTypes from 'prop-types';
import BlcLabel from 'components/BlcText/Label';
import BlcInputError from 'components/BlcInput/Error';
import BlcOption from 'components/BlcInput/Option';
import { BlcIconCaret } from 'components/BlcIcon';
import { color, size, unit, util, weight } from 'public/styles';

const BlcSelectBase = (props) => {
	const labelPositionHelper = (position) => {
		switch (position) {
		case 'top':
			return 1
		case 'left':
			return 1
		case 'bottom':
			return 2
		case 'right':
			return 2
		default:
			return 0
		}
	}

	const inputPositionHelper = (position) => {
		switch (position) {
		case 'top':
			return 2
		case 'left':
			return 2
		case 'bottom':
			return 1
		case 'right':
			return 1
		default:
			return 0
		}
	}

	return (
		<div className="form-item">
			{props.label === true ?
				<BlcLabel
					cursor={props.disabled ? 'not-allowed' : 'pointer'}
					for={props.id}
					order={labelPositionHelper(props.labelPosition)}
					padding={size.marginSmallest}
				>
					{props.text}
				</BlcLabel>
				: null
			}

			<div>
				<select
					id={props.id}
					key={props.id}
					name={props.name}
					className={
						props.errors[props.name] && props.touched[props.name]
							? 'field-error'
							: null
					}

					onBlur={props.handleBlur}
					onChange={event => props.setFieldValue(props.name, event.target.type === 'checkbox' ? event.target.checked : event.target.value)}

					defaultChecked={props.checked}
					value={props.type === 'radio' ? props.value : props.values[props.name]}
					disabled={props.disabled}
				>
					{props.placeholder ?
						<BlcOption disabled selected>
							{props.placeholder}
						</BlcOption>
						: null
					}
					{props.children}
				</select>

				<span>
					<BlcIconCaret
						color={
							props.errors[props.name] && props.touched[props.name]
								? color.alert
								: color.grayLight
						}
						direction="bottom"
						size="smaller"
					/>
				</span>
			</div>

			{props.errors[props.name] && props.touched[props.name]
				? <BlcInputError>{props.errors[props.name]}</BlcInputError>
				: null
			}

			<style jsx>{`
				.form-item {
					align-items: center;
					display: flex;
					flex-wrap: ${
		props.labelPosition === 'top' || props.labelPosition === 'bottom'
			? `wrap`
			: `nowrap`
		};
					margin: ${props.margin} 0;
				}

				.form-item > div {
					order: ${inputPositionHelper(props.labelPosition)};
					position: relative;
					width: 100%;
				}

				span {
					display: block;
					height: ${unit(3)};
					position: absolute;
					right: ${size.marginSmallest};
					top: 50%;
					transform: translateY(-50%);
					width: ${unit(3)};
				}

				select {
					appearance: none;
					background-color: ${color.white};
					box-shadow: none;
					border-color: ${color.grayLight};
					border-radius: 0;
					border-style: solid;
					border-width: 1px;
					color: ${color.asphalt};
					font-family: MarkPro, sans-serif;
					font-size: ${util.blcParagraphSize(props.size)};
					font-stretch: normal;
					font-style: normal;
					font-weight: ${weight.regular};
					letter-spacing: normal;
					line-height: ${util.blcParagraphLineHeight(props.size)};
					padding: ${size.marginSmallest} ${unit(5)} ${size.marginSmallest} ${size.marginSmaller};
					text-align: ${props.textAlign};
					transition: border-color .15s;
					width: 100%;
				}

				select:focus,
				select:hover {
					border-color: ${color.asphaltLight};
					outline: none;
				}

				select:disabled {
					background-color: ${color.smoke};
					color: ${color.gray};
					cursor: not-allowed;
				}

				select:disabled:focus,
				select:disabled:hover {
					border-color: ${color.grayLight};
				}

				select.field-error {
					border-color: ${color.alert};
				}
			`}</style>
		</div>
	);
}

BlcSelectBase.propTypes = {
	id: PropTypes.string.isRequired,
	name: PropTypes.string.isRequired,
	text: PropTypes.string.isRequired,

	errors: PropTypes.object,
	onBlur: PropTypes.func,
	onChange: PropTypes.func,
	touched: PropTypes.object,
	values: PropTypes.object,

	children: PropTypes.node,
	color: PropTypes.string,
	disabled: PropTypes.bool,
	label: PropTypes.bool,
	labelPosition: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
	margin: PropTypes.string,
	placeholder: PropTypes.string,
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
};

BlcSelectBase.defaultProps = {
	children: null,
	disabled: false,
	label: false,
	labelPosition: 'top',
	margin: size.marginSmaller,
	placeholder: null,
	textAlign: 'left'
};

export default BlcSelectBase;


