import React from 'react';
import PropTypes from 'prop-types';
import BlcLabel from 'components/BlcText/Label';
import BlcInputError from 'components/BlcInput/Error';
import { color, size, util, weight } from 'public/styles';

const BlcTextarea = props => {
	const labelPositionHelper = (position) => {
		switch (position) {
		case 'top':
			return 1
		case 'left':
			return 1
		case 'bottom':
			return 2
		case 'right':
			return 2
		default:
			return 0
		}
	}

	const inputPositionHelper = (position) => {
		switch (position) {
		case 'top':
			return 2
		case 'left':
			return 2
		case 'bottom':
			return 1
		case 'right':
			return 1
		default:
			return 0
		}
	}

	return (
		<div className="form-item">
			{props.label === true ?
				<BlcLabel
					cursor={props.disabled ? 'not-allowed' : 'pointer'}
					for={props.id}
					order={labelPositionHelper(props.labelPosition)}
					padding={size.marginSmallest}
				>
					{props.text}
				</BlcLabel>
				: null
			}

			<textarea
				id={props.id}
				key={props.id}
				name={props.name}
				placeholder={props.placeholder}
				className={
					props.errors[props.name] && props.touched[props.name]
						? 'field-error'
						: null
				}

				onBlur={props.handleBlur}
				onChange={e => props.setFieldValue(props.name, e.target.value)}

				value={props.values[props.name]}
				disabled={props.disabled}
			/>

			{props.errors[props.name] && props.touched[props.name]
				? <BlcInputError>{props.errors[props.name]}</BlcInputError>
				: null
			}

			<style jsx>{`
				.form-item {
					align-items: center;
					display: flex;
					flex-wrap: ${
		props.labelPosition === 'top' || props.labelPosition === 'bottom'
			? `wrap`
			: `nowrap`
		};
					margin: ${props.margin} 0;
				}

				textarea {
					appearance: none;
					background-color: ${color.white};
					box-shadow: none;
					border-color: ${color.grayLight};
					border-radius: 0;
					border-style: solid;
					border-width: 1px;
					color: ${color.asphalt};
					font-family: MarkPro, sans-serif;
					font-size: ${util.blcParagraphSize(props.size)};
					font-stretch: normal;
					font-style: normal;
					font-weight: ${weight.regular};
					height: 5em;
					letter-spacing: normal;
					line-height: ${util.blcParagraphLineHeight(props.size)};
					order: ${inputPositionHelper(props.labelPosition)};
					padding: ${size.marginSmallest} ${size.marginSmaller};
					resize: ${props.resize};
					text-align: ${props.textAlign};
					transition: border-color .15s;
					width: 100%;
				}

				textarea:focus,
				textarea:hover {
					border-color: ${color.asphaltLight};
					outline: none;
				}

				textarea:disabled {
					background-color: ${color.smoke};
					color: ${color.gray};
					cursor: not-allowed;
				}

				textarea:disabled:focus,
				textarea:disabled:hover {
					border-color: ${color.grayLight};
				}

				textarea.field-error {
					border-color: ${color.alert};
				}
			`}</style>
		</div>
	);
}

BlcTextarea.propTypes = {
	id: PropTypes.string.isRequired,
	name: PropTypes.string.isRequired,
	text: PropTypes.string.isRequired,

	errors: PropTypes.object,
	onBlur: PropTypes.func,
	onChange: PropTypes.func,
	touched: PropTypes.object,
	values: PropTypes.object,

	color: PropTypes.string,
	disabled: PropTypes.bool,
	label: PropTypes.bool,
	labelPosition: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
	margin: PropTypes.string,
	placeholder: PropTypes.string,
	resize: PropTypes.oneOf(['none', 'vertical']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right']),
	type: PropTypes.oneOf(['text', 'number'])
};

BlcTextarea.defaultProps = {
	disabled: false,
	label: false,
	labelPosition: 'top',
	margin: size.marginSmaller,
	placeholder: null,
	resize: 'none',
	textAlign: 'left',
	type: 'text'
};

export default BlcTextarea;
