import React from 'react';
import PropTypes from 'prop-types';
import BlcParagraph from 'components/BlcText/Paragraph';
import { color, size } from 'public/styles';

const BlcInputError = (props) => (
	<div className="input-error-message">
		<BlcParagraph
			color={color.alert}
			margin={props.margin}
			size={props.size}
			textAlign={props.textAlign}
		>
			{props.children}
		</BlcParagraph>

		<style jsx>{`
			.input-error-message {
				order: 100;
				width: 100%;
			}
		`}</style>
	</div>
)

BlcInputError.propTypes = {
	children: PropTypes.node,
	margin: PropTypes.string,
	size: PropTypes.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
};

BlcInputError.defaultProps = {
	children: null,
	margin: size.marginSmallest,
	size: null,
	textAlign: 'left'
};

export default BlcInputError;
