import React from 'react';
import PropTypes from 'prop-types';
import BlcCheckboxBase from 'components/BlcInput/BlcCheckboxBase';
import { size } from 'public/styles';

const blcRadio = props => (
	<BlcCheckboxBase
		{...props}

		disabled={props.disabled}
		margin={props.margin}
		type="radio"
	/>
);

blcRadio.propTypes = {
	disabled: PropTypes.bool,
	margin: PropTypes.string,
};

blcRadio.defaultProps = {
	disabled: false,
	margin: size.marginSmallest,
}

export default blcRadio;
