import React from 'react';
import PropTypes from 'prop-types';
import BlcInputBase from 'components/BlcInput/BlcInputBase';
import { size } from 'public/styles';

const blcInput = props => (
	<BlcInputBase
		{...props}

		disabled={props.disabled}
		label={props.label}
		labelPosition={props.labelPosition}
		margin={props.margin}
		placeholder={props.placeholder}
		textAlign={props.textAlign}
		type="text"
	/>
);

blcInput.propTypes = {
	disabled: PropTypes.bool,
	label: PropTypes.bool,
	labelPosition: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
	margin: PropTypes.string,
	placeholder: PropTypes.string,
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
};

blcInput.defaultProps = {
	disabled: false,
	label: false,
	labelPosition: 'top',
	margin: size.marginSmaller,
	placeholder: null,
	textAlign: 'left'
};

export default blcInput;
