import React from 'react';
import PropTypes from 'prop-types';
import BlcCheckboxBase from 'components/BlcInput/BlcCheckboxBase';
import { size } from 'public/styles';

const blcCheckbox = props => (
	<BlcCheckboxBase
		{...props}

		disabled={props.disabled}
		margin={props.margin}
		type="checkbox"
	/>
);

blcCheckbox.propTypes = {
	disabled: PropTypes.bool,
	margin: PropTypes.string,
};

blcCheckbox.defaultProps = {
	disabled: false,
	margin: size.marginSmallest,
}

export default blcCheckbox;
