import React from 'react';
import PropTypes from 'prop-types';
import BlcSelectBase from 'components/BlcInput/BlcSelectBase';
import { size } from 'public/styles';

const blcSelect = props => (
	<BlcSelectBase
		{...props}

		disabled={props.disabled}
		label={props.label}
		labelPosition={props.labelPosition}
		margin={props.margin}
		placeholder={props.placeholder}
		textAlign={props.textAlign}
	>
		{props.children}
	</BlcSelectBase>
);

blcSelect.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	label: PropTypes.bool,
	labelPosition: PropTypes.oneOf(['top', 'right', 'bottom', 'left']),
	margin: PropTypes.string,
	placeholder: PropTypes.string,
	textAlign: PropTypes.oneOf(['left', 'center', 'right'])
};

blcSelect.defaultProps = {
	children: null,
	disabled: false,
	label: false,
	labelPosition: 'top',
	margin: size.marginSmaller,
	placeholder: null,
	textAlign: 'left'
};

export default blcSelect;
