import React from 'react';
import PropTypes from "prop-types";
import { Formik } from 'formik';

const BlcForm = (props) => {
	return(<Formik
		initialValues={props.initialValues}
		validate={props.validate}
		validationSchema={props.validationSchema}
		onSubmit={props.onSubmit}
	>
		{({
			values,
			errors,
			touched,
			handleBlur,
			handleSubmit,
			setFieldValue,
			isSubmitting,
		}) => (
			<form onSubmit={handleSubmit}>

				{ React.Children.map(props.children, child => React.cloneElement(child,
					{
						setFieldValue: setFieldValue,
						handleBlur: handleBlur,
						isSubmitting: isSubmitting,
						values: values,
						errors: errors,
						touched: touched,
						disabled: isSubmitting
					}))
				}
			</form>
		)}
	</Formik>);
}

BlcForm.propTypes = {
	/**
	 * Initial values of map where key-values of the form are be stored
	 */
	initialValues: PropTypes.object.isRequired,
	/**
	 * Custom validation function (recieves the stored values) return errors map
	 */
	validate: PropTypes.func,
	/**
	 * Alternative to validate function, use validation schema using Yup
	 */
	validationSchema: PropTypes.object,
	/**
	 * Function to trigger on form submitting
	 */
	onSubmit: PropTypes.func.isRequired
}

export default BlcForm;
